<template>
  <div></div>
</template>

<script setup>
import * as PIXI from 'pixi.js';
import game2Image from './assets/game2.png';

// 创建pixi画布
const app = new PIXI.Application({
  width: window.innerHeight,
  height: window.innerHeight,
  background: '#efefef',
  resolution: window.devicePixelRatio || 1,
  resizeTo: window,
  antialias: true // 抗锯齿
});

//将应用画布添加到DOM中
document.body.appendChild(app.view);

// 创建一个矩形
const container = new PIXI.Container();

// 将矩形添加到画布中
app.stage.addChild(container);

// 添加恐龙游戏的精灵纹理
const baseTexture = PIXI.Texture.from(game2Image);

// 创建恐龙纹理
const dinoTexture = new PIXI.Texture(
  baseTexture,
  new PIXI.Rectangle(1680, 0, 82, 100) // 使用一个矩形截取出来
);

const dino = new PIXI.Sprite(dinoTexture);
dino.x = 60
dino.y = window.innerHeight - 50 - 70
// dino.visible = false;
container.addChild(dino);
// 恐龙跑步动画
const runTextures = []
for (let i = 0; i < 2; i++) {
  runTextures.push(
    new PIXI.Texture(
      baseTexture,
      new PIXI.Rectangle(1680 + (2 + i) * 88, 0, 82, 100) // 88是一张图
    )
  )
}
const runAnmition = new PIXI.AnimatedSprite(runTextures);
runAnmition.animationSpeed = 0.1;
runAnmition.play(); // 开始播放动画
runAnmition.visible = false;
container.addChild(runAnmition);

// 恐龙跳跃动画
const jumpTextures = new PIXI.Texture(
  baseTexture,
  new PIXI.Rectangle(1680, 0, 82, 100)
)

const jumpSprite = new PIXI.Sprite(jumpTextures);
jumpSprite.x = 60
jumpSprite.y = window.innerHeight - 70
jumpSprite.visible = false;
container.addChild(jumpSprite);

// 地面纹理
const groundTexture = new PIXI.Texture(
  baseTexture,
  new PIXI.Rectangle(50, 100, 2300, 30)
)

const groundSprite = new PIXI.TilingSprite(groundTexture);
groundSprite.width = window.innerWidth;
groundSprite.height = 30;
// 设置地面位置
groundSprite.position.set(0, window.innerHeight - 50);
container.addChild(groundSprite);

// 创建仙人掌精灵
const cactusTexture = new PIXI.Texture(
  baseTexture,
  new PIXI.Rectangle(515, 0, 30, 60)
)

const cactusSprite = new PIXI.Sprite(cactusTexture);
cactusSprite.x = window.innerWidth / 2 + 550;
cactusSprite.y = window.innerHeight - 50 - 60;

cactusSprite.scale.x = 1.4; // x 方向放大两倍
cactusSprite.scale.y = 1.4; // y 方向放大两倍

container.addChild(cactusSprite);


// 创建鸟的容器
const birdContainer = new PIXI.Container();

// 创造一只小鸟
function createBird() {
  // 创建小鸟精灵
  const bridTexture = new PIXI.Texture(
    baseTexture,
    new PIXI.Rectangle(265, 0, 82, 100)
  )
  const bridSprite = new PIXI.Sprite(bridTexture);
  bridSprite.visible = false

  container.addChild(bridSprite);

  // 小鸟飞行动画
  const flyTextures = []
  for (let i = 0; i < 2; i++) {
    flyTextures.push(
      new PIXI.Texture(
        baseTexture,
        new PIXI.Rectangle(266 + i * 88, 0, 82, 100)
      )
    )
  }
  const flyAnmition = new PIXI.AnimatedSprite(flyTextures);
  flyAnmition.x = window.innerWidth / 2 + 1500;
  flyAnmition.y = window.innerHeight - 50 - 180;
  flyAnmition.animationSpeed = 0.1;
  flyAnmition.play(); // 开始播放动画

  birdContainer.addChild(flyAnmition);
}

// 将鸟的容器添加到舞台
container.addChild(birdContainer);


// 创建开始游戏文字
let startText = new PIXI.Text("按空格开始游戏", {
  fontFamily: "Arial",
  fontSize: 36,
  fill: "0x333333",
  align: "center",
})

startText.x = window.innerWidth / 2;
startText.y = window.innerHeight / 2 - 50;
startText.anchor.set(0.5);
container.addChild(startText);

// 点击事件
startText.interactive = true
startText.on('click', () => {
  playGame()
})


// 创建重新开始按钮
const reloadTexture = new PIXI.Texture(
  baseTexture,
  new PIXI.Rectangle(150, 130, 60, 60) // 使用一个矩形截取出来
);
const reloadSprite = new PIXI.Sprite(reloadTexture);
reloadSprite.x = window.innerWidth / 2
reloadSprite.y = window.innerHeight / 2
reloadSprite.anchor.set(0.5);
reloadSprite.visible = false;
container.addChild(reloadSprite);


// 游戏设置
let isGameing = false
let isGameOver = false

function playGame() {
  isGameing = true
  startText.visible = false
  dino.visible = false
  // 显示恐龙的跑步动画
  runAnmition.visible = true;
  runAnmition.x = 60
  runAnmition.y = window.innerHeight - 50 - 70
}

function gameOver() {
  isGameOver = true
}

// 游戏得分
let score = 0;
// 跳跃速度(置空时间)
let jumpSpeed = 20;
// 初始化重力
let gravity = 1;

// 创建得分文字
let scoreText = new PIXI.Text("您的得分为：" + score, {
  fontFamily: "Arial",
  fontSize: 28,
  fill: "0x333333",
})

scoreText.x = window.innerWidth - scoreText.width - 100;
scoreText.y = 50;

// 将文本添加到舞台
container.addChild(scoreText);

// 创建得分音频对象
const scoreSound = new Howl({
  src: ['./audio/SCORE.wav'], // 替换为你的音频文件路径
  volume: 0.5, // 设置音量
});

// 创建跳跃音频对象
const jumpSound = new Howl({
  src: ['./audio/JUMP.wav'], // 替换为你的音频文件路径
  volume: 1, // 设置音量
});


// 得分函数
function increaseScore() {
  score++;
  scoreSound.play()
  scoreText.text = "您的得分为：" + score;
}

// 游戏结束逻辑
function showOver() {
  gameOver()
  startText.visible = false
  scoreText.visible = false
  reloadSprite.visible = true

  let word =
    score <= 5 ? '再接再厉' :
      score <= 20 ? '哎呦不错哦' :
        '太厉害了'

  let overText = new PIXI.Text('游戏结束，最后的得分为：' + score + "，" + word, {
    fontFamily: 'Arial',
    fontSize: 36,
    fill: '0x333333',
    align: 'center'
  })

  overText.x = window.innerWidth / 2
  overText.y = window.innerHeight / 2 - 60
  overText.anchor.set(0.5);
  container.addChild(overText)

  reloadSprite.interactive = true
  reloadSprite.on('click', () => {
    location.reload()
  })
}

const minInterval = 4000; // 最小间隔时间（毫秒）
const maxInterval = 6000; // 最大间隔时间（毫秒）

// 设置鸟的生成间隔和速度
const birdSpawnInterval = Math.random() * (maxInterval - minInterval) + minInterval; // 每隔 3-4 秒生成一只鸟

// 记录上一次生成鸟的时间
let lastBirdSpawnTime = 0

let isJumping = false

// 锁定60帧
app.ticker.maxFPS = 60;

// 游戏循环
app.ticker.add((deleta) => {
  if (isGameOver) {
    return
  }

  const currentTime = performance.now();

  let birdSpeed =
    score < 15 ? 18 :
      score < 25 ? 20 :
        22

  // 判断是否到达生成鸟的时间间隔
  if (currentTime - lastBirdSpawnTime >= birdSpawnInterval && score >= 5) {
    createBird(); // 生成鸟
    lastBirdSpawnTime = currentTime; // 更新上一次生成鸟的时间
  }

  // 移动鸟
  for (let i = birdContainer.children.length - 1; i >= 0; i--) {
    const bird = birdContainer.children[i];
    bird.x -= birdSpeed;

    // 判断鸟是否超出屏幕边界
    if (bird.x + bird.width < 0) {
      birdContainer.removeChild(bird); // 从容器中移除鸟
    }

    if (jumpSprite.y >= bird.y - 40 &&
      jumpSprite.y <= bird.y + 40 &&
      jumpSprite.x >= bird.x - 60 &&
      jumpSprite.x <= bird.x + 60) {
      showOver()
    }
  }

  if (isGameing) {

    if (score < 5) {
      // 地面移动
      groundSprite.tilePosition.x -= 12
      // 仙人掌移动
      cactusSprite.x -= 12

    }
    // 出现小鸟
    else if (score >= 5 && score < 15) {
      groundSprite.tilePosition.x -= 14
      cactusSprite.x -= 14
    }
    else if (score >= 15 && score < 25) {
      groundSprite.tilePosition.x -= 16
      cactusSprite.x -= 16
    }
    else if (score >= 25) {
      groundSprite.tilePosition.x -= 18
      cactusSprite.x -= 18
    }


    if (cactusSprite.x < -30) { // 如果仙人掌跳出了屏幕之外,就重置位置再次出现
      cactusSprite.x = window.innerWidth;
      increaseScore();
    }
  }
  if (jumpSprite.visible) {
    // 重力加速度效果
    jumpSpeed -= gravity
    jumpSprite.y -= jumpSpeed

    if (jumpSprite.y >= window.innerHeight - 50 - 70) {
      isJumping = false
      jumpSprite.y = window.innerHeight - 50 - 70 // 复原
      jumpSpeed = 20
      jumpSprite.visible = false
      runAnmition.visible = true

    }
  }

  // 碰撞检测
  if (jumpSprite.y >= cactusSprite.y - 60 &&
    jumpSprite.x > cactusSprite.x - 60 &&
    jumpSprite.x < cactusSprite.x + 20) {
    showOver()
  }
})

window.addEventListener('keydown', (e) => {
  if (e.code == "Space" && !isJumping) {
    playGame()
    jumpSound.play()
    runAnmition.visible = false
    jumpSprite.visible = true
    isJumping = true
    jumpSpeed = 20
  }

  if (e.code == "Space" && isGameOver) {
    location.reload()
  }
})


</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

canvas {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}
</style>
